<template>
  <div class="content">
    <van-nav-bar title="" left-text="" right-text left-arrow @click-left="goBack"/>
    <div class="row-text">{{conf.sun_first}}1{{conf.sun_first_other}}</div>
    <div class="row-text">{{conf.sun_second}}1{{conf.sun_second_other}}</div>
    <div class="row-text tip-r">1010201000</div>
    <div class="invite-title">----</div>
    <van-cell-group>
      <van-field type="textarea" :value="user_url" />
      <van-button
        class="btn"
        :loading="false"
        type="default"
        :bottom-action="true"
        v-clipboard:copy="user_url"
        v-clipboard:success="onCopy"
        v-clipboard:error="onError"
      ></van-button>
    </van-cell-group>
    <van-cell-group>
      <van-button class="btn" :bottom-action="true" @click="toRefresh " type="primary"></van-button>
    </van-cell-group>
    <div class="row-text tip-r">
        <p>11</p>
<!--        <p>20</p>-->
        <p></p>
    </div>
    <div class="row-text">
        <p></p>
        <p>1.500</p>
        <p>2.3</p>
        <p>3.<3</p>
        <p>4.tb</p>
        <p>5.</p>
        <p></p>
        <p></p>
        <p class="tip-r">-</p>
        <p class="tip-r"></p>
        <p class="tip-r">IPwifi</p>
        <p class="tip-r"></p>
        <p class="tip-r"></p>
        <p class="tip-r"></p>
    </div>
  </div>
</template>
<script>
import {
  Row,
  Col,
  CellGroup,
  Cell,
  Button,
  Dialog,
  ImagePreview,
  Toast,
  Field
} from "vant";
import { CONF } from "@/api/user";

export default {
  name: "invites",
  components: {
    [Row.name]: Row,
    [Col.name]: Col,
    [CellGroup.name]: CellGroup,
    [Cell.name]: Cell,
    [Button.name]: Button,
    [Toast.name]: Toast,
    [ImagePreview.name]: ImagePreview
  },
  created() {
    this.initPage()
  },
  data() {
    return {
      conf:[],
      user_url: ''
    };
  },
  methods: {
    initPage(){
      this.$reqGet(CONF)
        .then(res => {
            this.getConf(res)
        }).catch((err) => {
        })
    },
    getConf(res){
      let result = res.data;
      this.conf = result.data
      let host = window.location.host
      let path = window.location.pathname
      let str = this.conf.user_code+'/'+this.conf.auth_time+'/'+this.conf.auth_code
      this.user_url ='http://'+host+path+'#/login/invite/'+str
    },
    onCopy(){
      Toast("");
    },
    onError() {
      Toast("");
    },
    toRefresh(){
      location.reload();
    },
    //
    goBack() {
      this.$router.push({name: 'index'});
    },
  }
};
</script>
<style lang="scss" scoped>
.row-text{
  background-color: #ffff;
  padding: 15px;
}
.invite-title{
  text-align: center;
  background-color: #ffff;
  padding: 12px 0;
  font-weight: bold;
  margin-top: 5px;
}
.tip-r{
  color:red;
}
.btn{
  margin-bottom: 8px;
}
</style>



